<template>
  <div class="setting-tab">
    <b-tabs vertical nav-wrapper-class="nav-vertical">
      <b-tab @click="$router.push('/settings/group-management')">
        <template #title>
          <feather-icon icon="LayersIcon" /> Group Management
        </template>
      </b-tab>

      <b-tab active>
        <template #title>
          <div style="width: 168px"><feather-icon icon="SlidersIcon" /> Risk Parameters</div>
        </template>
        <div class="ml-3">
          <!-- Currency and Language settings -->
          <div>
            <h4 class="mb-3">Company Settings:</h4>
            <div class="row my-3">
              <div class="col-2">
                <p>Company Currency</p>
                <v-select
                  disabled
                  label="Company Currency"
                  v-model="company_settings.currency"
                  :options="['USD']"
                />
              </div>

            <div class="col-2">
                <p>Company Language</p>
                <v-select
                  disabled
                  v-model="company_settings.language"
                  label="Company Language"
                  :options="['English']"
                />
              </div>
            </div>
          </div>
          <!-- Risk Thresholds table -->
          <div class="row">
             <div class="col-6">
            <h4 class="mb-1">Risk Thresholds</h4>

            <div class="rounded my-shadow"> 
              <validation-observer ref="simpleRules">
                <b-table-simple no-border-collapse sticky-header bordered responsive striped  class="rounded mb-0">
                  <b-thead>
                    <b-tr>
                      <b-th>Symbol</b-th>
                      <b-th>Medium Risk</b-th>
                      <b-th>High Risk</b-th>
                      <b-th/>
                    </b-tr>
                  </b-thead>
                  <b-tbody v-if="Object.keys(risk_theshold).length">
                    <b-tr v-for="(item, index) in ['Default',...Object.keys(risk_theshold).filter(x => x != 'Default')]" :key="index">
                      <b-td :class="item=='Default' ? 'font-weight-bold pl-1' : ''">{{item}}</b-td>
                      <b-td class="">
                        <validation-provider v-if="editing_item == item" #default="{ errors }" name="Mediun Risk" rules="required" >
                          <b-form-input v-model.number="risk_theshold[item].medium_risk" :state="errors.length > 0 ? false:null" />
                          <small class="text-danger">{{ errors[0] }}</small>
                        </validation-provider>
                        <div v-else>{{risk_theshold[item].medium_risk}}</div>
                      </b-td>
                      <b-td class="">
                        <validation-provider v-if="editing_item == item" #default="{ errors }" name="High Risk" rules="required" >
                          <b-form-input v-model.number="risk_theshold[item].high_risk" :state="errors.length > 0 ? false:null"/>
                          <small class="text-danger">{{ errors[0] }}</small>
                        </validation-provider>
                        <div v-else>{{risk_theshold[item].high_risk}}</div>
                      </b-td>
                      <b-td class=""><feather-icon icon="EditIcon" size="14" class="text-success cursor-pointer" @click="editing_item = item"/></b-td>
                    </b-tr>
                  </b-tbody>
                </b-table-simple>
              </validation-observer>

              <div class="p-1">
                <b-button v-if="editing_item" variant="gradient-success" size="sm" pill @click="save()">Save</b-button>
              </div>
            </div>
          </div>

          </div>
        </div>
      </b-tab>
    </b-tabs>
  </div>
</template>

<script>
import { BTabs, BTab, BFormInput, BFormGroup, BForm } from "bootstrap-vue";
import { BTable, BProgress, BBadge, BTableSimple, BThead, BTr, BTh, BTd, BTbody, BTfoot, BButton } from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import vSelect from "vue-select";
import axios from '@axios'

// validation
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required} from '@validations'

export default {
  components: {
    BTabs, BTab, BFormInput, BFormGroup, BForm, ValidationProvider, ValidationObserver,
    BTable, BProgress, BBadge, BTableSimple, BThead, BTr, BTh, BTd, BTbody, BTfoot, BButton,
    vSelect,
    ToastificationContent
  },
  data(){
    return {
      company_settings: {
        currency: 'USD',
        language: 'English'
      },
      language: {
        en: 'English',
        vi: 'Vit Nam'
      },
      risk_theshold: {},
      editing_item: null,
      required
    
    }
  },
  mounted() {
    axios.get(`${process.env.VUE_APP_API_URL}site_settings/company?recursive`).then(res => {
      this.company_settings.currency = res.data.settings.main_currency
      this.company_settings.language = this.language[res.data.settings.default_language]
    })

    axios.get(`${process.env.VUE_APP_API_URL}site_settings/risk_management/risk_threshold`).then(res => {
      this.risk_theshold = res.data.settings
    })

    this.$store.commit('app/SET_DATA_LIVE_TIME', '')
  },

  methods: {
    validationForm() {
      this.$refs.simpleRules.validate().then(success => {
        if (success) {
          // eslint-disable-next-line
          alert('form submitted!')
        }
      })
    },
    save(){
      this.$refs.simpleRules.validate().then(success => {
        if (success) {
          axios.patch(`${process.env.VUE_APP_API_URL}site_settings/risk_management/risk_threshold`, {setting: this.risk_theshold}).then(res => {
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'Successfully',
                text: 'Save successfully!',
              },
            })
            this.editing_item = null
          })
        }
      })
    },
      
  },
};
</script>
<style>
  .dark-layout .vs__dropdown-toggle {
    border-radius: 20px!important;
  }

  .setting-tab th {
    height: 70px;
    padding-top: 24px;
  }

  .setting-tab a {
    background: #242526!important;
  }
  .setting-tab .b-table-sticky-header {
    height: 60vh;
    max-height: 60vh!important;
  }
  
</style>